.main-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  padding: 1.2em 2em 1.3em 2em;
  overflow: hidden;
  box-shadow: none;
  font-family: Manrope, Roboto, sans-serif;
  font-size: 1.8rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  transition-property: background, border-color;
  transition-duration: 300ms;
  white-space: nowrap;
  border-radius: 5em;
  border: transparent;
  color: #fff;
  background: linear-gradient(to right, #EC7325 50%, #F89718 100%);
  background-size: 200%;
  background-position: right 0;
  box-shadow: 0px 4px 8px 0px #F8971875;
}
.main-btn .icon-arrow-tail-bold {
  margin-left: 1.5rem;
  width: 2rem;
  height: 2rem;
}
.main-btn .icon-arrow-tail-bold:before {
  position: relative;
  top: 1px;
  font-size: 1.4rem;
}
@media only screen and (max-width: 380px) {
  .main-btn .icon-arrow-tail-bold {
    display: none;
  }
}
.main-btn:hover {
  color: #fff;
  background-position: 0 0;
}
.main-btn:active {
  background-position: right 0;
  box-shadow: none;
}

.blink:before {
  animation: blink 1.1s infinite;
  /* Параметры анимации */
}

@keyframes blink {
  from {
    opacity: 1;
    /* Непрозрачный текст */
  }
  to {
    opacity: 0;
    /* Прозрачный текст */
  }
}
