/*Основной блок*/
.paddingRight {
  padding-right: 1.7rem;
}

.marginLeftPop {
  margin-left: -0.9rem;
}

.popup--opened {
  visibility: visible !important;
  opacity: 1 !important;
}
.popup--opened .popup__body .pop-anim {
  transform: perspective(0) rotateY(0deg) !important;
}

.popup {
  position: fixed;
  z-index: 10000 !important;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out 0s;
  background: rgba(0, 0, 0, 0.8);
}
.popup .popup__body {
  min-height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup .popup__body .popup__content {
  position: relative;
  padding: 6rem 3.5rem;
  border-radius: 1rem;
  background-color: #fff;
  perspective-origin: right;
}
@media only screen and (max-width: 640px) {
  .popup .popup__body .popup__content {
    padding: 5rem 2.5rem;
  }
}
@media only screen and (max-width: 480px) {
  .popup .popup__body .popup__content {
    padding: 4rem 1.5rem;
  }
}
.popup .popup__body .popup__content .popup-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  color: #484847;
  font-size: 2rem;
  transition-duration: 0.3s;
}
.popup .popup__body .popup__content .popup-close:hover {
  color: #5D6D1A;
}

