.footer__container .footer-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.footer__container .footer-row .footer__copyright {
  display: flex;
  align-items: center;
  flex: 1 1 40rem;
  max-width: 40rem;
}
@media only screen and (max-width: 768px) {
  .footer__container .footer-row .footer__copyright {
    margin-bottom: 1rem;
    text-align: center;
    flex-basis: 100%;
    max-width: 100%;
  }
}
.footer__container .footer-row .footer__copyright p {
  color: #717b6c;
  font-size: 1.3rem;
}
.footer__container .footer-row .footer__links {
  display: grid;
  align-items: center;
  justify-content: flex-end;
  flex: 1 1 30rem;
  max-width: 30rem;
}
@media only screen and (max-width: 768px) {
  .footer__container .footer-row .footer__links {
    justify-content: center;
    text-align: center;
    flex-basis: 100%;
    max-width: 100%;
  }
}
.footer__container .footer-row .footer__links .link a {
  font-size: 1.3rem;
  color: #fff;
}

