// ФОРМЫ

/*Основной блок*/

.paddingRight {
	padding-right: 1.7rem;
}

.marginLeftPop {
	margin-left: -0.9rem;
}

.popup--opened {
	visibility: visible !important;
	opacity: 1 !important;
	.popup__body {
		.pop-anim {
			transform: perspective(0) rotateY(0deg) !important;
		}
	}
}

.popup {
	position: fixed;
	z-index: 10000 !important;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	visibility: hidden;
	opacity:0;
	transition: all .2s ease-in-out 0s;
	background:rgba(0,0,0,.8);
	.popup__body {
		min-height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;

		.popup__content {
			position: relative;
			padding: 6rem 3.5rem;
			border-radius: 1rem;
			background-color: $white;
			perspective-origin: right;

			@include bp-640 {
				padding: 5rem 2.5rem;
			}
			@include bp-480 {
				padding: 4rem 1.5rem;
			}
	
			.popup-close {
				position: absolute;
				top: 1.5rem;
				right: 1.5rem;
				width: 2rem;
				height: 2rem;
				cursor: pointer;
				color: $text;
				font-size: 2rem;
				transition-duration: 0.3s;
				&:hover {
					color: $main-color;
				}
			}
		}

	}

}


/*-----------*/




/*Форма заявки*/

.popup-callback {
	
	@include bp-480 {
		align-items: flex-start;
		overflow-x: hidden;
	}
}

.popup__body {

	.popup__content {

		.form-call-title {
			text-align: center;
		}
	
	}

	.pop-anim {
		transform: perspective(20rem) rotateY(-3deg);
		transition: transform .3s ease .0s;
	}

	.popup-title {
		display: flex;
		flex-direction: column;
		margin-bottom: 1.5rem;
		h3 {	
			@include title-font;
			color: $title;
		}
		p {
			@include adaptiv-font(14,13);
			color: $text;

		}
	}

}

.form-request {
	display: flex;
	flex-direction: column;
	align-items: center;

	.input-box {
		margin: 0.5rem;
		width: 100%;
		@include bp-768 {
			margin-left: 0;
			margin-right: 0;
		}
	}
	
	.form-btn {
		margin: 0.5rem;
		width: 100%;
		@include bp-768 {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.checkbox {
		width: 100%;
	}	

}

/*-----------*/




/*Форма с описанием проекта*/

.popup-content__project {
	// visibility: visible;
	// opacity: 1;
	overflow-x: hidden;
	
	.popup__body {
		position: relative;
	}

}

.popup__content-project {

	width: 120rem;
	
	@include bp-1200 {
		max-width: 70rem ;
		margin-left: 1rem;
		margin-right: 1rem;
	}




}

.popup__project-wrap {
	display: flex;
	justify-content: center;

	@include bp-992 {
		flex-direction: column;	
		align-items: center;
	}

	.slider-project-card {
		flex: 1 1 70%;
		max-width: 70rem;

		@include bp-992 {
			flex-basis: 100%;
			max-width: 100%;
		}
	}

	.popup__desc-project {
		display: flex;
		flex-direction: column;
		flex: 1 1 40%;
		max-width: 40rem;
		padding: 4.5rem 3rem;
		border-radius: 1rem;
		border: 3px solid #ebebeb;

		@include bp-992 {
			flex-basis: 100%;
			max-width: 100%;
		}

		.popup-title {
			margin-bottom: 2rem;
			h4 {
				margin-bottom: 2rem;
			}
			.title-key {
				p {
					font-size: 1.4rem;
					font-weight: 700;
				}
				span {
					font-size: 1.3rem;
				}
			}
		}

		.house-settings {
			margin-bottom: 2rem;
			.settings-item {
				position: relative;
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				margin-top: 0.8rem;
				margin-bottom: 0.8rem;
				p {
					margin-right: auto;
				}
				p, span {
					z-index: 2;
					background-color: $white;
					padding-left: 0.5rem;
					padding-right: 0.5rem;
					font-size: 1.4rem;
				}
				span {
					font-weight: 700;
				}
				&:before {
					z-index: 0;
					position: absolute;
					bottom: 0.4rem;
					left: 0;
					content: '';
					width: 100%;
					border: 0.1rem dotted #e0e0e0;
				}
			}
		}

		.house-rooms {
			display: flex;
			flex-direction: row;
			margin-bottom: 3rem;
			.room-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				margin: 0.2rem;
				padding: 0.5rem;
				border-radius: 0.5rem;
				border: 1px solid #e2e2e2;
				.room-img {
					width: 3rem;
					height: 3rem;
				}
				span {
					font-size: 1.3rem;
				}
			}
		}

		.house-price {
			margin-bottom: 2rem;
			.house-price-title {
				margin-bottom: 1rem;
				p {
					font-weight: 700;
				}
			}
			.price-block {
				display: flex;
				flex-direction: column;
				& div:last-child {
					border-bottom: 0;
				}
				.price-type {
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					padding: 0.9rem 0;
					border-bottom: 0.1rem dotted #e0e0e0;
					span {
						font-weight: 700;
					}
				}
			}
		}

		.house-btn {
			.main-btn__block {
				margin-bottom: 2rem;
				.main-btn {
					width: 100%;
				}
			}
			.second-btns {
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: space-between;
				a {
					display: block;
					padding: 1.5rem 2rem;
					border-radius: 3em;
					height: 5rem;
					border: 1px solid #c7c7c7;
					color: $grey-dark1;
					transition: all 0.2s ease-in-out;
					&:hover {
						background-color: #f1f1f1;
						border-color: #f1f1f1;
					}
					&:active {
						background-color: #c7c7c7;
						border-color: #c7c7c7;
					}
				}
				.btn-credit,
				.btn-mother-money {
					font-size: 1.4rem;
				}
				.btn-printer {
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 1.8rem;
					width: 5rem;
					.icon-printer {
						position: relative;
						top: 0.2rem;
					}
				}
			}
		}
	}
}




/*-----------*/




/*Политика*/

.popup-politics {
	align-items: flex-start;
	justify-content: center;
	overflow-x: hidden;
	.popup__body {

		.popup__content {
			flex-basis: 700px;
			max-width: 700px;
			.popup__desc {
				-ms-flex: 0 1 auto;
				.popup-title {
					margin-bottom: 20px;
				}
	
				.text-row {
					margin-bottom: 30px;
					
					.header-text {
						margin-bottom: 10px;
						p {
							font-size: 16px;
							font-weight: 700;
						}
					}
	
				}
	
			}

		}

	} 
}


