/*Картинки*/

.slider-project {
	width: 100%;
	height: auto;

}

.main-slider__images {
	border-radius: 1.5rem;
	overflow: hidden;
	margin: 0 1rem 1rem 1rem;
	@include bp-480 {
		border-radius: 0.5rem;
	}
	.main-image {
		img {
			width: 100%;
			height: auto;
			object-fit: cover;
		}
	}

}

.mini-slider__block {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.mini-slider__image {
	width: 25%;
	height: 25%;
	margin: 1rem;
	border-radius: 1rem;
	overflow: hidden;

	@include bp-480 {
		border-radius: 0.5rem;
	}

	img {
		width: 100%;
		height: auto;
	}
}

/*----------*/