// БУРГЕР МЕНЮ

/*Основа*/

.burger-menu__block {
	overflow-y: auto;
	z-index: 80 !important;
	position: fixed;
	top: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;
	max-width: 480px;
	width: 100%;
	background-color: $third-color;
	transform: translateX(100%);
	transition: transform .3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
	@include bp-480 {
		max-width: 100%;
	}

	&.show {
	  transform :translateX(0px);
	}
}

/*-----------*/




/*Пункты меню*/

.close-burger {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 19px 55px;
	border-bottom: 1px solid #2d381b;
	cursor: pointer;
	transition: all .2s ease;

	@include bp-480 {
		justify-content: center;
	}

	p {
		margin-left: 5px;
		font-size: 14px;
		transition-duration: 0.3s;
		color: $white;
		&:before {
			position: relative;
			top: 1px;
			left: -5px;
		}
	}

	&:hover {
		background-color: #2d381b;
		p {
			color: $second-color;
		}
	}

}
 
.burger-menu__items {
	span {
		display: block;
		padding-left: 2.2em;
		padding-right: 2.2em;
		padding-top: 1em;
		padding-bottom: 1em;
		@include adaptiv-font(24,20);
		font-weight: 700;
		text-decoration: none;
		border: none;
		transition: all .3s ease-in-out;
		color: $white;
		cursor: pointer;
		border-bottom: 1px solid #2d381b;

		@include bp-480 {
			text-align: center;
		}

		&:hover {
			background-color: #2d381b;
			color: $second-color;
		}
	}
}

/*-----------*/




/*Пункты меню*/

.burger__contacts {
	padding-left: 4rem;
	margin-top: 3rem;
	margin-bottom: 3rem;
	display: flex;
	flex-direction: column;
	margin-left: 1.5rem;
	margin-right: 1.5rem;
	
	@include bp-480 {
		align-items: center;
		padding-left: 0;
	}

	.phone,.mail {
		margin-left: 0;
		margin-right: 0;
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;

		@include bp-768 {
			margin-right: 0;
		}

		.contacts-ic {

			@include bp-768 {
				display: none;
			}
		}


	}

	.phone-number {
		&:hover {
			color: $second-color;
		}
	}

	.mail {
		a {
			font-size: 1.6rem;
			color: $white;
		}
	}

	.call-btn {
		margin-top: 2.5rem;
	}

	.burger__call-btn {
		font-size: 1.6rem;
	}

	.social {
		margin-top: 30px;
		transition: all .15s ease;
	
		a {
			display: inline-block;
			width: 30px;
			height: 30px;
			margin-right: 25px;
			transition: 0.3s ease-in-out;
			&:last-of-type {
				margin-right: 0px;
			}
			img {
				width: 30px;
				height: 30px;
			}
	
		  }
	}

}



.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height:100%;
	transition: all 0.3s ease-in-out;
	background-color: $black;
	opacity: 0;
	visibility: hidden;
	&.show {
		opacity: 0.8;
		visibility: visible;
	}
	z-index: 60;
}

/*-----------*/

