// ПЕРВЫЙ ЭКРАН

/*Основной блок*/

#first-screen {
	background-image: url('../img/pictures/backgrounds/header-bg-1x.jpg');
	@include bg-x2 ('../img/pictures/backgrounds/header-bg-2x.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 68rem;

	@include bp-1400 {
		height: 100%;
	}

	@include bp-992 {
		background-position-x: -80rem;
	}
}

.banner__row {
	justify-content: space-between;
	flex-wrap: nowrap;

	@include bp-992 {
		flex-direction: column;
	}
}


/*-----------*/




/*Информация слева*/

.banner__info-block {
	display: flex;
	flex-direction: column;
	flex: 1 2 70%;
	max-width: 79rem;
	margin-right: 2rem;

	@include bp-992 {
		flex-basis: 100%;
		max-width: 100%;
		margin-right: 0;
	}
}

.banner__main-title {
	margin-bottom: 4rem;

	@include bp-480 {
		margin-bottom: 2rem;
	}

	h1 {
		color: $white;

		span {
			color: $second-color;
		}

		@include bp-640 {
			text-align: center;
		}
	}
}


.banner__quote-shield {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 4rem;

	@include bp-640 {
		flex-direction: column;
	}

	@include bp-480 {
		margin-bottom: 3rem;
	}

}

.banner__quote {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-right: 2rem;
	flex: 1 1 52rem;
	max-width: 52rem;

	@include bp-640 {
		flex-basis: auto;
		max-width: auto;
		margin-right: 0;
		margin-bottom: 3rem;
	}

	.icon-quote {
		width: 2.3rem;
		height: 2rem;
		transform: rotate(270deg);
		margin-right: 1rem;
		&:before {
			font-size: 1.6rem;
			color: $white;
			opacity: 0.3;
		}
		@include bp-640 {
			display: none;
		}
	}

	p {
		@include adaptiv-font(20,14);
		color: $white;
		span {
			font-weight: 700;
			color: $white;
		}

		@include bp-640 {
			text-align: center;
		}

	}
}

.banner__info-shield {

	@include bp-640 {
		width: 14rem;
	}
}

.banner__building-types {
	display: flex;
	flex-direction: row;
	margin-bottom: 4rem;

	@include bp-640 {
		justify-content: center;
		flex-wrap: wrap;
	}
}

.building-type {
	position: relative;
	flex: 1 2 15rem;
	max-width: 15rem;
	overflow: hidden;
	border-radius: 1.5rem;
	background-color: $main-color;
	margin: 0.5rem;
	transition: all 0.3s ease-in-out;
	p {
		position: absolute;
		padding-top: 1.5rem;
		padding-left: 1.5rem;
		font-size: 1.4rem;
		font-weight: 700;
		color: $white;
	}

	@include bp-640 {
		flex-basis: 10rem;
		max-width: 10rem;
	}

	.building-pic {
		max-width: 15rem;
		height: 15rem;
		background-repeat: no-repeat;
		background-size: cover;
		background-position-y: 0;

		@include bp-640 {
			max-width: 10rem;
			height: 10rem;
		}
	}

	.home-pic {
		background-image: url('../img/pictures/banner-cards/home-1x.png');
		@include bg-x2 ('../img/pictures/banner-cards/home-2x.png');
	}

	.summer-pic {
		background-image: url('../img/pictures/banner-cards/summer-1x.png');
		@include bg-x2 ('../img/pictures/banner-cards/summer-2x.png');
	}

	.sauna-pic {
		background-image: url('../img/pictures/banner-cards/sauna-1x.png');
		@include bg-x2 ('../img/pictures/banner-cards/sauna-2x.png');
	}

	.pergola-pic {
		background-image: url('../img/pictures/banner-cards/pergola-1x.png');
		@include bg-x2 ('../img/pictures/banner-cards/pergola-2x.png');
	}

	.kids-pic {
		background-image: url('../img/pictures/banner-cards/kids-1x.png');
		@include bg-x2 ('../img/pictures/banner-cards/kids-2x.png');
	}

	&:hover {
		cursor: pointer;
		background-color: #67791B;
		box-shadow: 0 7px 18px rgba(152, 182, 29, 0.5);
	}

}

/*-----------*/




/*Форма*/

.banner__form {
	flex: 1 0 30%;
	max-width: 36rem;

	@include bp-992 {
		flex-basis: 100%;
		max-width: 100%;
	}
}

.banner__form-wrapper {
	padding: 15px;
	border-radius: 1rem;
	background-color: rgba(255, 255, 255, 0.4); 
	box-shadow: 0 6px 44px rgba(0, 0, 0, 0.2);
	@include bp-480 {
		padding: 0;
	}
	
}

.tag-sale {
	position: relative;
	display: flex;
	justify-content: center;
	width: 100%;
	height: 2px;

	.tag-sale-box {
		position: relative;
		display: flex;
		justify-content: center;
		align-content: center;
		width: 20.4rem;
		height: 7rem;
		
		p {
			position: absolute;
			top: 1.3rem;
			@include title-font;
			z-index: 3;
			font-size: 2.4rem;
			color: $white;
			span {
				font-weight: 800;
				color: $white;
			}
			font-weight: 400;

		}

		img {
			transform: rotate(180deg);
		}


	}


}

.banner__form-container {
	background-color: $white;
	padding: 4rem 3rem;
	border-radius: 1rem;
	box-shadow: 0 6px 44px rgba(0, 0, 0, 0.2);
	@include bp-380 {
		padding: 2rem 2rem;
	}
}

.banner__form-title {
	display: flex;
	flex-direction: column;
	margin-bottom: 2rem;

	@include bp-992 {
		flex-direction: row;
		justify-content: center;
	}

	@include bp-640 {
		flex-direction: column;
	}

	p, span {
		@include title-font;
		font-size: 1.8rem;
		font-weight: 800;
		text-align: center;

		@include bp-992 {
			font-size: 2.2rem;
		}

		@include bp-380 {
			font-size: 1.6rem;
		}

	}

	p {
		@include bp-992 {
			margin-right: 0.5rem;
		}
		@include bp-640 {
			margin-right: 0;
		}
	}

	span {
		color: $orange;
	}

}

/*-----------*/


// SECTION END ==========


